* {
  box-sizing: border-box; }

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px; }

body,
ul,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

.grid-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  height: 100%; }

[class*="item-"] {
  flex-shrink: 0;
  flex-grow: 0; }

.item-1 {
  width: 16.66667%; }

.item-2 {
  width: 33.33333%; }

.item-3 {
  width: 50%; }

.item-4 {
  width: 66.66667%; }

.item-5 {
  width: 83.33333%; }

@media screen and (max-width: 767px) {
  .item-sm-1 {
    width: 16.66667%; }
  .item-sm-2 {
    width: 33.33333%; }
  .item-sm-3 {
    width: 50%; }
  .item-sm-4 {
    width: 66.66667%; }
  .item-sm-5 {
    width: 83.33333%; }
  .item-sm-6 {
    width: 100%; } }

body {
  background: url(https://ilyalytvynov.github.io/metro-ui-example/images/pattern.jpg); }

.content-wrapper {
  max-width: 820px;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.page-header {
  padding: 0.5rem;
  color: #ffffff;
  font-size: 2em;
  margin: 0.67em 0;
  font-weight: bold; }

.blog-image {
  height: 100%;
  flex: 0 0 30%;
  background: url(https://ilyalytvynov.github.io/metro-ui-example/images/blog-image.png) center no-repeat;
  background-size: cover; }

.item {
  height: 150px;
  padding: 0.5rem; }

.item .item-content {
  height: 100%;
  position: relative; }

.item:nth-child(1) .item-content {
  background: #3387ea; }

.item:nth-child(2) .item-content {
  background: #f9be3e; }

.item:nth-child(3) .item-content {
  background-color: #d3573e; }

.item:nth-child(4) .item-content {
  background-color: #59b0e2; }

.item:nth-child(5) .item-content {
  background-color: #33af95; }

.item:nth-child(6) .item-content {
  background-color: #86a73f; }

.item:nth-child(7) .item-content {
  background-color: #151a26; }

.item:nth-child(8) .item-content {
  background-color: #7e5b8c; }

.item-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.item-title {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0.5rem;
  color: #ffffff; }

.blog-item {
  display: flex; }

.blog-description {
  flex: 0 0 70%;
  padding: 0.5rem;
  box-sizing: border-box; }

.blog {
  font-size: 1.17em;
  font-weight: bold; }

.blog,
.blog-text {
  display: block;
  color: #ffffff;
  margin: 1.1em 0; }
